import React from 'react'
import HomeIcon from '@mui/icons-material/Home';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import { Avatar, Grid ,Box } from '@mui/material';
import ChatRoundedIcon from '@mui/icons-material/ChatRounded';
import ListAltRoundedIcon from '@mui/icons-material/ListAltRounded';
import logo from '../resource/logo.jpg';

export default function BottomNavbar() {
  return (
    <div style={{
      display:'flex',
      position:'fixed',
      bottom:'0',
      justifyContent:'center',
      width:'100%',
      height:'9vh',
      backgroundColor:'#e6e3e3',
      zIndex:'1'
    }}>
      <Box>
      <Grid container sx={{
        width:'50vw', height:'100%',paddingTop:'10px',fontSize:'12px'}}>
        <Grid item md={2} sx={{textAlign:'center'}}>
          <HomeIcon sx={{color:'rgb(37, 150, 190)' ,width:'45%',height:'45%',cursor:'pointer'}}/><br/>Home
        </Grid>
        <Grid item md={2} sx={{textAlign:'center'}}>
          <PeopleAltIcon sx={{color:'rgb(37, 150, 190)' ,width:'45%',height:'45%',cursor:'pointer'}}/><br/>Profile
        </Grid>
        <Grid item md={4} sx={{textAlign:'center',display:'flex',justifyContent:'center',cursor:'pointer'}}>
          <Avatar src={logo} />
        </Grid>
        <Grid item md={2} sx={{textAlign:'center'}}>
          <ListAltRoundedIcon sx={{color:'rgb(37, 150, 190)' ,width:'45%',height:'45%',cursor:'pointer'}}/><br/>FollowList
        </Grid>
        <Grid item md={2} sx={{textAlign:'center'}}>
          <ChatRoundedIcon sx={{color:'rgb(37, 150, 190)' ,width:'45%',height:'45%',cursor:'pointer'}}/><br/>Chat
        </Grid>
      </Grid>
      </Box>
    </div>
  )
}
