import React from 'react'
import { Box, Paper , Grid, Avatar, Button} from '@mui/material';

import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import TvIcon from '@mui/icons-material/Tv';
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';
import CampaignIcon from '@mui/icons-material/Campaign';
import NewspaperIcon from '@mui/icons-material/Newspaper';
import TextsmsIcon from '@mui/icons-material/Textsms';
import WorkIcon from '@mui/icons-material/Work';
import FeedbackIcon from '@mui/icons-material/Feedback';
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment';
import WaterDropIcon from '@mui/icons-material/WaterDrop';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import LightbulbIcon from '@mui/icons-material/Lightbulb';

import Image from '../resource/image.jpg';
import Consultant from '../resource/th.jpeg';
import AppIcon from '../resource/appicon.jpeg';

export default function Content() {
  return (
    <div style={{width:'100%', marginTop:'15vh',marginBottom:'11vh'}}>
        <Box sx={{display:'flex',flexDirection:'row'}}>
            <Box component={Paper} sx={{
                width:'40%',
                margin:'50px',
                height:'42vh',
                backgroundImage:`url("${Image}")`,
                backgroundSize:'contain',
                backgroundRepeat:'no-repeat',
                position:'relative',
                }}>
                <Box>
                    <Button variant='outlined' sx={{
                        borderRadius:'20px',
                        position:'absolute',
                        bottom:'50px',
                        left:'30px'}}>
                        Recharge Now     
                    </Button>
                    <Button variant='contained' sx={{
                        borderRadius:'20px',
                        position:'absolute',
                        bottom:'50px',
                        left:'200px',
                    }}>
                        Learn More
                    </Button>
                    
                </Box>
            </Box>
            <Box component={Paper} sx={{width:'50%',height:'50%', textAlign:'center',fontSize:'12px' ,margin:'40px'}}>
                <Grid container >
                    <Grid item  md={3}>
                        <PhoneAndroidIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Prepaid Mobile Recharge
                    </Grid>
                    <Grid item  md={3}>
                        <TvIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        DTH Recharge
                    </Grid>
                    <Grid item  md={3}>
                        <CurrencyRupeeIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Loan
                    </Grid>
                    <Grid item  md={3}>
                        <CampaignIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Promotion
                    </Grid>
                    <Grid item  md={3}>
                        <NewspaperIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        News
                    </Grid>
                    <Grid item  md={3}>
                        <TextsmsIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Blog
                    </Grid>
                    <Grid item  md={3}>
                        <WorkIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Jobs
                    </Grid>
                    <Grid item  md={3}>
                        <FeedbackIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Anonymous Feedback
                    </Grid>
                    <Grid item  md={3}>
                        <LocalFireDepartmentIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Gas Booking
                    </Grid>
                    <Grid item  md={3}>
                        <LightbulbIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}} />
                        <br/>
                        Electricity Bill
                    </Grid>
                    <Grid item  md={3}>
                        <WaterDropIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        Water Bill
                    </Grid>
                    <Grid item  md={3}>
                        <ArrowForwardIosIcon sx={{width:'50%',height:'50%',color:'rgb(37, 150, 190)',paddingTop:'10px',cursor:'pointer'}}/>
                        <br/>
                        See More
                    </Grid>
                </Grid>
            </Box>
        </Box>
        <Box sx={{width:'100%',display:'flex',flexDirection:'row',justifyContent:'center'}}>
            <Box sx={{
                border:'1px solid blue',
                padding:'25px auto',
                textAlign:'center',
                margin:'2px 10px',
                width:'40%',
                cursor:'pointer',
                backgroundColor:'#0A4D68',
                color:'#FFFFFF',
                height:'5vh',
                display:'flex',
                alignItems:'center',
                justifyContent: 'center',
                borderRadius:'5px'
              }}>
                All Popular Service Providers
            </Box>
            <Box sx={{
                border:'1px solid blue',
                padding:'25px auto',
                margin:'2px 10px',
                width:'40%',
                cursor:'pointer',
                backgroundColor:'#0A4D68',
                color:'#FFFFFF',
                height:'5vh',
                display:'flex',
                alignItems:'center',
                justifyContent: 'center',
                borderRadius:'5px'
              }}>
                Services We Provide
            </Box>
        </Box>
        <Box component={Paper} sx={{width:'30%',margin:'30px 50px'}}>
            <img src={Consultant} alt='Consultancy Service' style={{width:'100%',height:'100%',cursor:'pointer'}}/>
        </Box>
        <Box sx={{
            position:'fixed',
            zIndex:'10',
            bottom:'75px',
            right:'50px',
        }}>
            <Avatar src={AppIcon} sx={{border:'1px solid black', cursor:'pointer'}}></Avatar>
        </Box>
    </div>
  )
}
