import { Box, Button, Badge, } from '@mui/material'
import React from 'react'
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import AddShoppingCartOutlinedIcon from '@mui/icons-material/AddShoppingCartOutlined';
import NotificationsIcon from '@mui/icons-material/Notifications';
import SearchIcon from '@mui/icons-material/Search';
import { styled, alpha } from '@mui/material/styles';
import InputBase from '@mui/material/InputBase';
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';


const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: '100%',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  width:'100%',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '100%',
      '&:focus': {
        width: '100%',
      },
    },
  },
}));

export default function TopNavbar() {
  return (
    <div style={{
      backgroundColor:'rgb(37, 150, 190)',
      zIndex:'1',
      position:'fixed',
      top:'0',
      width:'100%',
    }}>
        <Box sx={{
            width:'100%',
            display:'flex',
            flexDirection:'row',
            height:'10vh',
          }}>
            <Box sx={{width:'3%'}}></Box>
            <Box sx={{
              display:'flex',
              justifyContent:'center',
              alignItems:'center',
              padding:'2px',
              width:'3%'
              }}> 
              <LocationOnOutlinedIcon sx={{width:'70%',height:'70%',color:'white',cursor:'pointer'}}/>
            </Box>
            <Box sx={{
              display:'flex',
              justifyContent:'center',
              alignItems:'center',
              padding:'2px',
              width:'80%'
              }}>
              <Button variant='outlined' sx={{
                width:'100%',
                height:'50%',
                border:'solid white 1px',
                color:'white',
                justifyContent:'left'
                }}>
                  Melvin Porter
                  P.O. Box 132 1599 Curabitur Rd.
                  Bandera South Dakota 45149
                  (959) 119-8364
              </Button>
            </Box>
            <Box sx={{
              display:'flex',
              justifyContent:'center',
              alignItems:'center',
              width:'4%'
              }}>
                <Badge color="error" badgeContent={0} max={1000} showZero sx={{width:'70%'}}>
                  <NotificationsIcon sx={{width:'100%',height:'100%',color:'white',cursor:'pointer'}}/>
                </Badge>
            </Box>
            <Box sx={{
              display:'flex',
              justifyContent:'center',
              alignItems:'center',
              width:'4%'
              }}>
                <AddShoppingCartOutlinedIcon sx={{width:'70%',height:'70%',color:'white',cursor:'pointer'}}/>
            </Box>
        </Box>
        <Box sx={{
          width:'100%',
          display:'flex',
          flexDirection:'row'
        }}>
          <Box sx={{width:'5%'}}></Box>
          <Box sx={{
            color:'white',
            paddingBottom:'10px',
            width:'80%'
          }}>
            <Search
                sx={{backgroundColor:'white',width:'100%'}}
            >
              <SearchIconWrapper>
                <SearchIcon sx={{color:'rgb(37, 150, 190)'}}/>
              </SearchIconWrapper>
              <StyledInputBase
                placeholder="Search for Products or service provider name"
                inputProps={{ 'aria-label': 'search' }}
                sx={{color:'black'}}
              />
            </Search>
          </Box>
          <Box sx={{
            paddingLeft:'30px',
            paddingTop:'5px'
          }}>
            <KeyboardVoiceIcon sx={{color:'white'}}/>
          </Box>
        </Box>
    </div>
  )
}